.bt1-container{
    display: flex;
    flex: 1;
    flex-direction: column;
    padding: 0 0rem 0 3rem;
}

.bt1-buttons{
    margin-top: -20rem;
    margin-left: -1rem;
}

.bt-bts{
    margin: 1rem;
    height: 35px;
    width: 15%;

    color: white;
    font-weight: bold;
    font-size: 17px;
    border: solid;
    border-radius: 50px;
    border-color: rgb(255, 136, 0);
    
    background-color: rgba(0, 0, 0, 0.459);
}.bt-bts:hover{
    border-color: rgb(255, 136, 0);
    border-radius: 10px;
    border-width: 2px;
    background-color: rgba(0, 0, 0, 0);
    cursor: pointer;
    transition-duration: 0.25s;
}