.n-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: transparent;
    height: 15vh;
    padding: 0 3rem 0 3rem;
    background-color: rgba(22, 22, 22, 0.212);
    z-index: 999999;
    flex: 1;
}

.n-left{
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}
.n-list{
    margin-top: 2rem;
    margin-left: -4rem;
}
.n-list>ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
    text-align: center;
}
.list2{
    background-image: url('../../img/bt-pl-nrm.png');
    height: 35px;
    width: 150px;
    text-align: center;
    align-items: center;
    background-size: cover;
    font-weight: bold;
    color: aliceblue;
    line-height: 32px;
}
.n-list>ul>li:hover{
    background-image: url('../../img/bt-pl-over.png');
    transition-duration: 0.2s;
    cursor: pointer;
}
.n-logoimg{
    cursor: pointer;
    width: 120px;
    height: 100px;
}

.nav {
    display: block;
}

.bugger {
    display: none;
}

@media only screen and (max-width: 1150px) {
    .nav {
        display: none !important;
    }

    .bugger {
        display: flex;
        align-items: center;
    }
}