.b5-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 180vh;
    margin-top: 50px;
}

.b5-title{
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    width: 90%;
}.b5-title>:nth-child(1){
    color: rgb(255, 174, 0);
    font-weight: bold;
    font-size: 45px;
}.b5-title>:nth-child(2){
    color: white;
    font-weight: bold;
    font-size: 20px;
    text-align: left;
}

.b5-roadmap{
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.b5-roadmap>img{
    width: 80vw;
}

@media only screen and (max-width: 1150px) {
    .b5-container{
        height: 80vh;
        margin-top: 50px;
    }

    .b5-roadmap>img{
        width: 80vw;
        height: 60vh;
    }

    .b5-title{
        display: flex;
        flex: 1;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .b5-title>:nth-child(2){
        color: white;
        font-weight: bold;
        font-size: 20px;
        text-align: center;
    }    
}