.b3-container{
    display: flex;
    flex: 1;
    height: 5vh;
    padding: 0 3rem 0 3rem;
    margin-top: 10rem;
}
.b3-title{
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    width: 100%;
}.b3-title>:nth-child(1){
    color: rgb(255, 174, 0);
    font-size: 45px;
    font-weight: bold;
}.b3-title>:nth-child(2){
    color: white;
    font-size: 20px;
    font-weight: bold;
    margin-top: 20px;
}

@media only screen and (max-width: 1150px) {
    .b3-title>:nth-child(2){
        color: white;
        font-size: 20px;
        font-weight: bold;
        text-align: center;
    }
}
