.ns-container{
    display: flex;
    flex: 1;

    height: 15vh;
    margin-top: -2.5rem;
    margin-bottom: 2.5rem;

}
.ns-box{
    display: flex;

    margin: 1rem;
    flex-direction: row;
    height: 70px;
    width: 25%;
    align-items: center;
    justify-content: center;

    margin-left: 37%;

    background-color: rgba(0, 0, 0, 0.308);
    border: none;
    border-radius: 25px;
}
.ns-image{
    width: 40px;

    padding: 1rem;
    cursor: pointer;

}
.ns-box>:nth-child(2){
    transform: scale(0.5);
}