.tb-container{
    display: flex;
    flex: 1;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    height: auto;
    padding: 0 3rem 0 3rem;
}
/*CONTAINER 01*/
.tb-box-container1{
    display: flex;
    height: 400px;
    width: 280px;
    margin-top: 1rem;
    margin-left: 1rem;

    background-image: url(../../img/card-team-01-nrm.png);
    background-size: cover;
}.tb-box-container1:hover{
    background-image: url(../../img/card-team-01-over.png);
    transition-duration: 0.25s;
}
/*CONTAINER 01*/

