.r-container{
    height: 220px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center; 
    box-sizing: border-box;
    padding: 15px;
    background-color: rgb(59, 25, 112);
    background-image: url('../../img/rodape-bg.png');
}
.b-logoimg{
    cursor: pointer;
    transform: scale(3.5);
    display: flex;
    height: 50px;
    margin-left: 7rem;
}
.list2{
    background-image: url('../../img/bt-pl-nrm.png');
    height: 35px;
    width: 150px;
    text-align: center;
    align-items: center;
    background-size: cover;
    font-weight: bold;
    color: aliceblue;
    line-height: 32px;
}
.r-text01{
    margin-top: 4rem;
    margin-left: 2.5rem;
    color: white;
    font-weight: bold;
}

.r-text02{
    margin-top: 4rem;
    color: white;
    font-weight: 400;
    font-size: 14px;
    align-items: center;
    justify-content: center;
    margin-left: 33.3rem;
}

.r-list{
    margin-top: -12rem;
    margin-bottom: 2rem;
    margin-left: 69rem;
}

.r-list>ul{
    margin-right: 4rem;
    text-align: center;
    gap: 1rem;
    row-gap: 2rem;
    padding: 0 1rem 0 1rem;
}
.list{
    background-image: url('../../img/bt-pl-nrm.png');
    height: 35px;
    width: 150px;
    text-align: center;
    align-items: center;
    background-size: cover;
    font-weight: bold;
    color: aliceblue;
    line-height: 32px;
}
.r-list>ul>li:hover{
    background-image: url('../../img/bt-pl-over.png');
    transition-duration: 0.2s;
    cursor: pointer;
}

.rights{
    display: block;
}

@media only screen and (max-width: 720px) {
    .rights {
        display: none !important;
    }
}