.tt-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    flex-direction: row;
    flex: 1;
    flex-wrap: wrap-reverse;

    /* height: 110vh; */

}
/*TEXT STYLES*/
.t-title{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
    margin: 1rem 1rem 1rem 3rem;
    height: 450px;
    width: 40.5%;


}.t-title>:nth-child(1){
    color: white;
    font-size: 45px;
    font-weight: bold;
}.t-title>:nth-child(3){
    color: white;
    width: 50vw;
    font-size: 15px;
    font-weight: 500;
}
/*TEXT STYLES*/

/*IMAGE SECTION 'GRAFICO'*/
.tt-image{
    display: flex;
    justify-content: center;
    margin: 1rem 1rem 1rem 1rem;
    height: 450px;
    width: 40vw;
    margin-top: 3rem;
}
/* IMAGE SECTION 'GRAFICO' */


/* BOX CONTAINER SECTION */
.tt-box-container{
    display: flex;
    height: 42vh;

    margin-top: 3rem;

}

.tt-bx-img{
    height: 250px;
    width:  560px;
}
.tt-image-img{
    width: 60vw;
    background-size: cover;
    margin-left: 5.5rem;
}


@media only screen and (max-width: 1150px) {
    .tt-image{
        width: 100vw;
        display: flex;
        justify-content: center;
        height: 350px;
        margin: 0;
        margin-left: 0;
        padding: 0;
    }
    .tt-image-img{
        width: 80vw;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
    }
    .tt-bx-img {
        width: 80vw;
    }

    /*TEXT STYLES*/
    .t-title{
        display: flex;
        flex-direction: column;
        
        margin: 1rem 1rem 1rem 3rem;
        height: 450px;
        width: 80%;


    }
    
    .t-title>:nth-child(1){
        color: white;
        font-size: 45px;
        font-weight: bold;
    }

    .tt-box-container{
        display: flex;
        justify-content: center;
        height: 42vh;
        margin-top: 3rem;
    
    }
    
    .t-title>:nth-child(3){
        color: white;
        width: 90%;
        font-size: 15px;
        font-weight: 500;
    }
}
/* BOX CONTAINER SECTION */
