.in-container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
    height: auto;
    width: 100vw;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10rem;
    margin-top: 5rem;
}

.in-title{
    display: flex;
    flex: 1;
    flex-direction: column;
    padding: 3rem;
    margin-top: 6rem;
}.in-title>:nth-child(1){
    color: rgb(255, 153, 0);
    font-size: 55px;
    font-weight: bold;
}
.in-title>:nth-child(2){
    color: white;
    font-size: 21px;
    font-weight: 400;
    margin-bottom: 3rem;
}
.in-title>:nth-child(3){
    color: white;
    font-size: 17px;
    font-weight: 400;
    margin-bottom: 1rem;
}


.in-left{
    display: flex;
    flex: 1;
}

.in-right{
    display: flex;
    flex: 1;
}
.in-image{
    transform: scale(0.8);
}

.image {
    width: 60vw;
}

@media only screen and (max-width: 720px) {
    .image {
        width: 100vw;
    }

    .in-title{
        display: flex;
        flex: 1;
        flex-direction: column;
        padding: 3rem;
        margin-top: 0rem;

    }
}