.b2-container{
    display: flex;
    justify-content: center;
    box-sizing: border-box;
    padding: 10px;
    align-items: center;
    flex: 1;
    height: auto;
    width: 100vw;
    flex-wrap: wrap-reverse;
    margin-top: 10rem;
}

.b2-left{
    display: flex;
    flex: 1;
    flex-direction: column;

}

.b2-text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 3rem;
    
}.b1-text{
    color: rgb(255, 174, 0);
    font-size: 45px;
    font-weight: bold;
}.b2-text{
    color: white;
    font-size: 15px;
    font-weight: bold;
}.b3-text{
    color: white;
    font-size: 15px;
    font-weight: 400;
    margin-top: 2rem;
}.b4-text{
    color: white;
    font-size: 17px;
    font-weight: bold;
    margin-top: 2rem;
}

.b2-button{
    border: solid;
    border-radius: 50px;
    border-color: rgb(255, 174, 0);
    background-color: rgba(0, 0, 0, 0);
    width: 50px;
    height: 50px;
    margin-top: -2.1rem;
    margin-left: 12rem;
    font-weight: bold;
    font-size: 25px;
    color: rgb(255, 174, 0);
}.b2-button:hover{
    background-color: rgb(255, 174, 0);
    transition-duration: 0.25s;
    color: black;
    font-weight: bold;
    cursor: pointer;
}

.b2-right{
    display: flex;
    flex-direction: column;
}

.b2-image{
    transform: scale(0.7);
    margin-top: -10rem;
    margin-left: 9rem;
}

.b2-icons{
    display: flex;
    flex: 1;
    margin-left: -28rem;
    margin-top: -13rem;
}
.b2-git{
    transform: scale(0.8);
    width: 55px;
    height: 55px;
    cursor: pointer;
}
.b2-medium{
    transform: scale(0.6);
    width: 85px;
    height: 50px;
    cursor: pointer;
}
.b2-youtube{
    transform: scale(0.6);
    width: 75px;
    height: 50px;
    cursor: pointer;
}

.imageB2 {
    width: 40vw;
    height: 65vh;
    background-size: cover;
}

.div {
    width: 40vw;
}

@media only screen and (max-width: 600px) {
    .imageB2 {
        margin-bottom: 20px;
        width: 90vw;
        height: 45vh;
    }

    .div {
        width: 90vw;
    }
}