.th-container{
    display: flex;
    flex: 1;
    flex-direction: column;
    height: 25vh;
    margin-top: 5rem;
}
.th-title{
    height: 85px;
    width: 100%;


    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;
}
.th-title>:nth-child(1){
    color: rgb(255, 166, 0);
    font-weight: bold;
    font-size: 45px;
}
.th-title>:nth-child(2){
    color: white;
    font-weight: 500;
    font-size: 22px;
}

