.nw-container{
    display: flex;
    flex-wrap: wrap;
    height: auto;
    justify-content: center;
    margin: 1rem 1rem 5rem 1rem;

}
/*  *** SEÇÃO DE NOTICIA 01 *** */
.nw-box1{
    height: 204px;
    width: 280px;
    margin-bottom: 30px;

    background-image: url('../../img/news-01-nrm.png');
    background-size: cover;
}.nw-box1:hover{
    background-image: url('../../img/news-01-over.png');
    background-size: contain;

    cursor: pointer;

    transition-duration: 0.25s;
}
