.nw-button{
    height: 50px;
    width: 150px;

    margin-top: 17rem;
    margin-left: 7.5rem;

    align-items: center;
    justify-content: center;

    border: none;
    border-radius: 25px;
    color: aliceblue;
    font-size: 18px;

    background-color: rgb(255, 166, 0);

}.nw-button:hover{
    background-color: rgba(0, 0, 0, 0.418);
    border: solid;
    border-color: rgb(255, 166, 0);

    transition-duration: 0.25s;
    cursor: pointer;

    font-weight: bold;
}