.h-container{
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

.h-image{
    width: 60vw;
    margin-left: -4rem;
    margin-top: 6rem;
}

.h-button{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    margin-top: -3.5rem;
}

.hb-playgame{
    height: 35px;
    width: 150px;
    justify-content: center;
    border: none;
    margin: 0 1rem;
    background:transparent;
    background-image: url('../../img/bt-pl-nrm.png');
    background-size: cover;

    text-align: center;
    align-items: center;
    background-size: cover;
    font-weight: bold;
    color: aliceblue;
    line-height: 32px;
    cursor: pointer;
}

.hb-playgame:hover{
    background-image: url('../../img/bt-pl-over.png');
    transition-duration: 0.25s;
}

.image {
    width: 60vw;
    margin: 0; 
    padding: 0;
}

@media only screen and (max-width: 600px) {
    .image {
        width: 80vw !important;
        margin: 0; 
        padding: 0;
    }
}