.b-container{
    height: 95vh;
    margin-top: -10rem;
    padding: 0 3rem 0 3rem;
}

.b-text{
    margin-top: 22.5rem;
}
.b-text>:nth-child(1){
    color: rgb(255, 174, 0);
    font-size: 65px;
    font-weight: bold;
}

.b-text>:nth-child(3){
    color: white;
    font-size: 20px;
}
.b-text>:nth-child(4){
    display: flex;
    flex: 1;
    color: white;
    font-weight: 400;
    margin-top: 2rem;
}

.hb-box01{
    height: 35px;
    width: 150px;
    justify-content: center;
    border: none;
    margin: 0 0.5rem;
    background:transparent;
    background-image: url('../../img/bt-pl-nrm.png');
    background-size: cover;

    margin-top: 5rem;

    text-align: center;
    align-items: center;
    background-size: cover;
    font-weight: bold;
    color: aliceblue;
    line-height: 32px;
    cursor: pointer;
}

.hb-box01:hover{
    background-image: url('../../img/bt-pl-over.png');
    transition-duration: 0.25s;
}

.b-right{
    display: flex;
    flex: 1;
    justify-content: space-between;
    margin-left: 34.5rem;
    margin-top: -37rem;
}

.b-rimage{
    transform: scale(0.7);
}